/*************** STYLES ***************/
* {
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  overflow-y: scroll;
}

body,
html {
  background: #6f00ff;
  min-height: 100vh;
}

body {
  position: relative;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  text-align: center;
  font-family: Mplus, Factor, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5em;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  transition: all 0.1s ease;
}

.mb-0 {
  margin-bottom: 0 !important;
}

.mb-12 {
  margin-bottom: 12px;
}

.mb-40 {
  margin-bottom: 40px !important;
}

/*************** BUTTONS, LINKS ***************/
a {
  color: #ff642d;
  text-decoration: none;
  position: relative;
  cursor: pointer;
}

button,
a.button {
  padding: 20px 60px 16px;
  border-radius: 50px;
  background-color: #ff642d;
  display: inline-flex;
  text-decoration: none;
  position: relative;
  text-transform: uppercase;
  transition: 0.5s;
  color: #fff;
  font-weight: 500;
  font-size: 22px;
  line-height: 19px;
  margin-bottom: 0;
  z-index: 100;
}
button:hover,
a.button:hover {
  position: relative;
  transition: 0.5s;
  background-color: #f24500;
}
button:active,
a.button:active {
  position: relative;
  transition: 0.5s;
  background-color: #cc3300;
}

