/******** header footer *******/
.nav {
  padding: 30px 0;
  width: 100%;
  top: 0;
  background: rgba(111, 0, 255, 0.7);
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  z-index: 100;
}
.nav .header-logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 100;
}
.nav .header-logo .h-logo {
  display: inline-block;
  margin-top: 8px;
  width: 235px;
  height: auto;
}
.nav .header-logo__sticky {
  display: none;
  justify-content: flex-start;
  z-index: 1000;
}
.nav .header-logo__mob {
  display: none;
  justify-content: flex-start;
  z-index: 1000;
}
.nav .header-button {
  display: flex;
  justify-content: flex-end;
}

.header-fixed {
  position: fixed;
  width: 100%;
  left: 0;
  top: -147px;
  z-index: 200;
  transition: 0.3s;
  -webkit-box-shadow: rgba(27, 39, 12, 0.1) 0 1px 10px;
  box-shadow: rgba(27, 39, 12, 0.1) 0 1px 10px;
}
.header-fixed.active {
  top: 0px;
}

/*************** Footer ***************/
